import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { MapsAPILoader } from '@agm/core';
import { Grocery } from '../models/Grocery';
import { GroceryService } from '../services/groceries.service';
declare var google: any;
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {

  @ViewChild("search") inputElement: ElementRef;

  private calculated: boolean = false;
  private showAllGroceries: boolean = false;

  private bestSpreadedGrocery: string = "";

  private lat: number;
  private lng: number;
  private nearestGrocery: Grocery = new Grocery("", "", 0, 0, 0, "");

  private groceries: Grocery[] = [
    new Grocery("Mega", "Dizegoff Street 65, Tel Aviv-Yafo", 32.0760805, 34.77474140000004, 0, "https://tse3.mm.bing.net/th?id=OIP.6sd3uSpf1OPmQGGXo0UAvwD5D5&pid=15.1"),
    new Grocery("Mega", "Pinkas Street 48, Tel Aviv-Yafo", 32.0908043, 34.78896540000005, 0, "https://tse3.mm.bing.net/th?id=OIP.6sd3uSpf1OPmQGGXo0UAvwD5D5&pid=15.1"),
    new Grocery("Mega", "HaRav Uziel 5, Bat Yam", 32.022946, 34.747818999999936, 0, "https://tse3.mm.bing.net/th?id=OIP.6sd3uSpf1OPmQGGXo0UAvwD5D5&pid=15.1"),
    new Grocery("Rami Levi", "HaUman Street 15", 31.7495349, 35.21045300000003, 0, "http://eilat.city/images/3615-7181-%D7%A8%D7%9E%D7%99-%D7%9C%D7%95%D7%99-%D7%90%D7%99%D7%9C%D7%AA-lg.jpg"),
    new Grocery("Rami Levi", "HaParsa Street 3, Jerusalem", 31.7509689, 35.21013049999999, 0, "http://eilat.city/images/3615-7181-%D7%A8%D7%9E%D7%99-%D7%9C%D7%95%D7%99-%D7%90%D7%99%D7%9C%D7%AA-lg.jpg"),
    new Grocery("Rami Levi", "HaMetachnen Street, Ashkelon", 31.63490580000001, 34.55491329999995, 0, "http://eilat.city/images/3615-7181-%D7%A8%D7%9E%D7%99-%D7%9C%D7%95%D7%99-%D7%90%D7%99%D7%9C%D7%AA-lg.jpg"),
    new Grocery("Shufersal", "Yigal Alon Street 76, Tel Aviv-Yafo", 32.0652457, 34.79263270000001, 0, "https://tse1.mm.bing.net/th?id=OIP.s3ON5BYB-EfJyviCm-LPMQHaHa&pid=15.1"),
    new Grocery("Shufersal", "Florentin 27 streety, Tel Aviv-Yafo", 32.05631900000001, 34.76974070000006, 0, "https://tse1.mm.bing.net/th?id=OIP.s3ON5BYB-EfJyviCm-LPMQHaHa&pid=15.1"),
    new Grocery("Shufersal", "Jabotinsky Street, Ofakim, Israel", 31.3179599, 34.624975199999994, 0, "https://tse1.mm.bing.net/th?id=OIP.s3ON5BYB-EfJyviCm-LPMQHaHa&pid=15.1"),
    new Grocery("Tiv Taam", "Jabotinski Street 37, Ashdod, Israel", 31.8117278, 34.64740860000006, 0, "http://blog.tapuz.co.il/drrd/images/662689_872.jpg"),
    new Grocery("Tiv Taam", "HaThiya Street 1, Ashkelon, Israel", 31.660091, 34.588856299999975, 0, "http://blog.tapuz.co.il/drrd/images/662689_872.jpg"),
    new Grocery("Tiv Taam", "Jabotinski Street 37, Ashdod, Israel", 31.8117278, 34.64740860000006, 0, "http://blog.tapuz.co.il/drrd/images/662689_872.jpg")

  ]

  constructor(private mapsApiLoader: MapsAPILoader, private service: GroceryService) { }

  ngOnInit() {

    this.mapsApiLoader.load().then(() => {
      let autoComplete = new google.maps.places.Autocomplete(this.inputElement.nativeElement, {
        types: ["address"],
        componentRestrictions: { country: "isr" }
      });

      autoComplete.addListener("place_changed", () => {
        let place = autoComplete.getPlace();
        this.lat = place.geometry.location.lat();
        this.lng = place.geometry.location.lng();
        console.log(this.lat, this.lng);

      })
    })

  }
  calculateDistance() {

    console.log('this groceries: ' + this.groceries)
    this.calculated = false;
    this.groceries.forEach(grocery => {
      let distance: number = this.distanceInKmBetweenEarthCoordinates(this.lat, this.lng, grocery.getLat, grocery.getLng);
      grocery.setDistanceFromUser = distance;

    })
    this.groceries = this.groceries.sort((a, b) => a.getDistanceFromUser - b.getDistanceFromUser);
    this.nearestGrocery = this.groceries[0];
    this.calculated = true;
    this.checkRadius();
  }

  degreesToRadians(degrees) {
    return degrees * Math.PI / 180;
  }

  distanceInKmBetweenEarthCoordinates(lat1: number, lon1: number, lat2: number, lon2: number) {
    var earthRadiusKm = 6371;

    var dLat = this.degreesToRadians(lat2 - lat1);
    var dLon = this.degreesToRadians(lon2 - lon1);

    lat1 = this.degreesToRadians(lat1);
    lat2 = this.degreesToRadians(lat2);

    var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.sin(dLon / 2) * Math.sin(dLon / 2) * Math.cos(lat1) * Math.cos(lat2);
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return parseFloat(Math.abs(earthRadiusKm * c).toFixed(3));
  }

  switchGroceriesShow() {
    this.showAllGroceries = !this.showAllGroceries;
  }


  checkRadius() {
    var mega = 0;
    var shufersal = 0;
    var ramiLevi = 0;

    this.groceries.forEach((grocery) => {
      let distance = this.distanceInKmBetweenEarthCoordinates(this.lat, this.lng, grocery.getLat, grocery.getLng);
      if (distance > 50) {
        console.log('not is radius')
      }
      switch (grocery.getName) {
        case "Mega":
          mega++;
          return;
        case "Shufersal":
          shufersal++;
          return;
        case "Rami Levy":
          ramiLevi++;
          return;
      }
      console.log('in radius')
    })
    console.log(mega, ramiLevi, shufersal)
    if (mega >= shufersal && mega >= ramiLevi) {
      this.bestSpreadedGrocery = "Mega";
      return;
    }
    if (shufersal >= mega && shufersal >= ramiLevi) {
      this.bestSpreadedGrocery = "Shufersal";
      return;
    }
    if (ramiLevi >= shufersal && ramiLevi >= mega) {
      this.bestSpreadedGrocery = "Rami Levi";
      return;
    }
  }

  //check this
  fetchGroceries() {
    this.groceries = [];
    this.service.getAllGroceries().subscribe(
      (groceries) => {
        for (let grocery of groceries) {
          grocery = new Grocery(grocery.name, grocery.address, grocery.lat, grocery.lng, grocery.distanceFromUser, grocery.image);
          console.log('grocery: ' + grocery)
          this.groceries.push(grocery);
        }
      },
      (error) => {
        console.log(error)
      }
    )
  }
}



