export class Grocery {
    private name: string;
    private address: string;
    private lat: number;
    private lng: number;
    private distanceFromUser: number;
    private image: string;

    constructor(name: string, address: string, lat: number, lng: number, distanceFromUser: number, image: string) {
        this.name = name;
        this.address = address;
        this.lat = lat;
        this.lng = lng;
        this.distanceFromUser = distanceFromUser;
        this.image = image;
    }

   public get getName():string{
    return this.name;
  }

  public set setName(name:string){
   this.name = name;
 }

  public get getLat():number{
   return this.lat;
 }

 public set settLat(lat:number){
   this.lat = lat;
 }

 public get getLng():number{
   return this.lng;
 }

 public set setLng(lng:number){
   this.lng = lng;
 }

 public get getDistanceFromUser():number{
   return this.distanceFromUser;
 }

 public set setDistanceFromUser(distanceFromUser:number){
   this.distanceFromUser = distanceFromUser;
 }

 public get getAddress():string{
   return this.address;
 }

 public set setAddress(address:string){
   this.address = address;
 }

 public get getImage():string{
   return this.image;
 }

 public set setImage(image:string){
   this.image = image;
 }
}


